@import "https://cdn.jsdelivr.net/gh/rastikerdar/vazirmatn@32.102/Round-Dots/misc/UI-Farsi-Digits/Vazirmatn-RD-UI-FD-font-face.css";
#nikas-thread {
  padding: 0;
  margin: 10px 0 10px 0;
  font-family: Vazirmatn RD UI FD, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  border-top: 1px solid #e8e8e8;
}
#nikas-thread * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Vazirmatn RD UI FD, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}
#nikas-thread .nikas-comment-header a {
  text-decoration: none;
  font-size: 12px;
}
#nikas-thread > h4 {
  color: #555;
  font-weight: 500;
  text-align: center;
  direction: rtl;
  margin: 20px 0 5px 0;
  font-size: unset;
}
#nikas-thread > .nikas-feedlink {
  float: right;
  padding-left: 1em;
}
#nikas-thread > .nikas-feedlink > a {
  font-size: 0.8em;
  vertical-align: bottom;
}
#nikas-thread .nikas-textarea {
  min-height: 58px;
  outline: 0;
  color: black;
  direction: rtl;
  font-size: 14px;
}
#nikas-thread .nikas-textarea:focus {
  border-color: rgba(0, 0, 0, 0.8);
}
#nikas-thread .nikas-textarea .nikas-placeholder {
  color: #757575;
  direction: rtl;
  font-size: 14px;
  display: block;
  cursor: text;
  transition: all 0.25s;
}
#nikas-thread input:focus {
  border-color: rgba(0, 0, 0, 0.8);
}

#nikas-root {
  direction: rtl;
}
#nikas-root .nikas-comment {
  max-width: 68em;
  padding-top: 0.95em;
  margin: 0.95em auto;
}
#nikas-root .nikas-comment:not(:first-of-type) {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
#nikas-root .nikas-preview .nikas-comment {
  padding-top: 0;
  margin: 0;
}

.nikas-follow-up .nikas-comment {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.nikas-comment > .nikas-avatar {
  display: block;
  width: 7%;
  float: right;
  margin: 3px 0 0 15px;
}
.nikas-comment > .nikas-avatar > svg {
  max-width: 48px;
  max-height: 48px;
  border-radius: 15px;
}
.nikas-comment > .nikas-avatar > img {
  max-width: 48px;
  max-height: 48px;
  border-radius: 15px;
}
.nikas-comment > .nikas-text-wrapper {
  display: block;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header {
  font-size: 0.85em;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header .nikas-spacer {
  padding: 0 6px;
  color: gray !important;
  font-weight: normal;
  text-shadow: none !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header .nikas-spacer:hover {
  color: #606060 !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header a.nikas-permalink {
  color: gray !important;
  font-weight: normal;
  text-shadow: none !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header a.nikas-permalink:hover {
  color: #606060 !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header .nikas-note {
  color: gray !important;
  font-weight: normal;
  text-shadow: none !important;
  float: left;
  margin-left: 5px;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header .nikas-note:hover {
  color: #606060 !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header a.nikas-parent {
  color: gray !important;
  font-weight: normal;
  text-shadow: none !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header a.nikas-parent:hover {
  color: #606060 !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-header .nikas-author {
  color: #555;
  font-size: 12px;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer {
  font-size: 0.95em;
  font-size: 0.8em;
  color: gray !important;
  clear: left;
  margin-top: 10px;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer a {
  font-weight: bold;
  text-decoration: none;
  padding-left: 0;
  transition: 0.1s linear all;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer a:hover {
  color: #111111 !important;
  text-shadow: #aaaaaa 0 0 1px !important;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer > a {
  position: relative;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer > a.edit {
  padding-left: 1em;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer > a.reply {
  padding-left: 1em;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer > a.cancel {
  padding-left: 1em;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer > a + a {
  padding-left: 1em;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer .nikas-votes {
  color: gray;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer .nikas-upvote svg {
  position: relative;
  margin-right: 3px;
}
.nikas-comment > .nikas-text-wrapper > .nikas-comment-footer .nikas-downvote svg {
  position: relative;
  margin-right: 3px;
}
.nikas-comment > .nikas-text-wrapper > .nikas-textarea-wrapper .nikas-textarea {
  margin-top: 0.2em;
  color: black;
  direction: rtl;
  padding: 0.7em 0.8em;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
}
.nikas-comment > .nikas-text-wrapper > .nikas-textarea-wrapper .nikas-preview {
  margin-top: 0.2em;
  color: black;
  direction: rtl;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
}
.nikas-comment > .nikas-text-wrapper > .nikas-text p {
  margin-top: 1em;
  font-size: 14px;
  font-weight: 400;
  direction: rtl;
  text-align: justify;
  text-align-last: right;
}
.nikas-comment > .nikas-text-wrapper > .nikas-text p:last-child {
  margin-bottom: 0.2em;
}
.nikas-comment > .nikas-text-wrapper > .nikas-text h1,
.nikas-comment > .nikas-text-wrapper > .nikas-text h2,
.nikas-comment > .nikas-text-wrapper > .nikas-text h3,
.nikas-comment > .nikas-text-wrapper > .nikas-text h4,
.nikas-comment > .nikas-text-wrapper > .nikas-text h5,
.nikas-comment > .nikas-text-wrapper > .nikas-text h6 {
  font-size: 130%;
  font-weight: bold;
}
.nikas-comment .nikas-follow-up {
  padding-left: unset;
  padding-right: calc(7% + 20px);
}
.nikas-comment .nikas-postbox {
  margin-top: 0.8em;
}

.nikas-feedlink {
  font-weight: bold;
  text-decoration: none;
  padding-left: 0;
  transition: 0.1s linear all;
}
.nikas-feedlink:hover {
  color: #111111 !important;
  text-shadow: #aaaaaa 0 0 1px !important;
}

.nikas-comment.nikas-no-votes > * > .nikas-comment-footer span.nikas-votes {
  display: none;
}

.nikas-postbox {
  max-width: 68em;
  margin: 0 auto 2em;
  clear: right;
}
.nikas-postbox > .nikas-form-wrapper {
  display: block;
  padding: 0;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section {
  display: block;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-post-action {
  display: block;
  float: right;
  margin: 0 0 0 5px;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-post-action > input {
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #ddd;
  cursor: pointer;
  outline: 0;
  line-height: 1.4em;
  padding: 5px 10px 5px 10px;
  transition: 0.2s linear all;
  font-size: 12px;
  margin-bottom: 0;
  box-shadow: none;
  color: black;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-post-action > input:hover {
  background-color: #ccc;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-post-action > input:active {
  background-color: #bbb;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-input-wrapper {
  display: inline-block;
  position: relative;
  max-width: 25%;
  margin: 0 5px 0 0;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-input-wrapper input {
  padding: 0.3em 10px;
  max-width: 100%;
  border-radius: 5px;
  background-color: #fff;
  line-height: 1.4em;
  border: 1px solid rgba(0, 0, 0, 0.2);
  font-family: Vazirmatn RD UI FD, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  text-align: center;
  font-size: 12px;
  color: black;
  box-shadow: none;
}
.nikas-postbox > .nikas-form-wrapper .nikas-textarea {
  margin: 0 0 0.3em;
  padding: 0.4em 0.8em;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.nikas-postbox > .nikas-form-wrapper .nikas-preview {
  margin: 0 0 0.3em;
  padding: 0.4em 0.8em;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  display: none;
  background-color: #f8f8f8;
  background: repeating-linear-gradient(-45deg, #f8f8f8, #f8f8f8 10px, #fff 10px, #fff 20px);
}
.nikas-postbox > .nikas-form-wrapper input[type=checkbox] {
  vertical-align: middle;
  position: relative;
  bottom: 1px;
  margin-left: 0;
}
.nikas-postbox > .nikas-form-wrapper .nikas-notification-section {
  font-size: 0.9em;
  padding-top: 0.3em;
}
.nikas-postbox > .nikas-form-wrapper input[name=edit] {
  display: none;
}
.nikas-postbox > .nikas-form-wrapper > .nikas-notification-section {
  display: none;
  padding-bottom: 10px;
}
.nikas-postbox > .nikas-copyright {
  display: block;
  padding: 0;
  margin: 0;
  font-size: 10px;
  color: #757575;
  text-align: left;
}
.nikas-postbox > .nikas-copyright a {
  margin-left: 3px;
  text-decoration: none;
  color: #4a4a4a;
}

.nikas-postbox.nikas-preview-mode > .nikas-form-wrapper input[name=preview] {
  display: none;
}
.nikas-postbox.nikas-preview-mode > .nikas-form-wrapper .nikas-textarea {
  display: none;
}
.nikas-postbox.nikas-preview-mode > .nikas-form-wrapper .nikas-preview {
  display: block;
}
.nikas-postbox.nikas-preview-mode > .nikas-form-wrapper input[name=edit] {
  display: inline;
}

@media screen and (max-width: 600px) {
  .nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-input-wrapper {
    display: block;
    max-width: 100%;
    margin: 0 0 0.3em;
  }
  .nikas-postbox > .nikas-form-wrapper > .nikas-auth-section .nikas-input-wrapper input {
    width: 100%;
  }
}